/*
 * `dash-table` adds extra styling on top of regular Bootstrap table css.
 *
 * `.dash-table-wrapper` can be used to add horizontal scrolling:
 *
 * ```
 * <div class="dash-table-wrapper">
 *   <table class="dash-table">
 *   ...
 *   </table>
 * </div>
 * ```
 *
 */
.dash-table {
  color: #4e5156;
  margin-bottom: 0;
}
.dash-table th {
  background-color: #f2f4f9;
  white-space: nowrap;
}
.dash-table .dash-table-icon {
  padding-left: 0.25rem;
  display: inline-block;
}
.dash-table .icon-sort {
  display: inline-block;
  width: 0;
  height: 0;
  line-height: 6px;
  position: relative;
  top: -1px;
  vertical-align: middle;
}
.dash-table .icon-asc {
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #71747c;
}
.dash-table .icon-desc {
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid #71747c;
}

.dash-table-wrapper {
  border-radius: 6px 6px 0 0;
  overflow-x: auto;
}

