.cookie-status {
  display: inline-block;
  padding: 0.35rem 0 0.25rem 0;
  transition: all 0.3s ease-out;
  padding-left: 18px;
  position: relative;
  opacity: 0;
}
.cookie-status[data-enabled=true] {
  opacity: 0.75;
}
.cookie-status::before {
  background-color: #df0606;
  content: "";
  display: block;
  height: 10px;
  width: 10px;
  border-radius: 50px;
  top: 0.82rem;
  left: 0;
  position: absolute;
  animation-name: pulse;
  animation-iteration-count: infinite;
  animation-duration: 1.2s;
  animation-timing-function: ease-in-out;
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  45% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  95% {
    opacity: 0;
  }
}
.copy-indicator {
  opacity: 0;
  padding: 0.375rem 0.75rem;
  display: inline-block;
}
.copy-indicator[data-enabled=true] {
  animation-name: blink;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
