.tabular-info .tabular-info-not-exists {
  border-radius: 6px;
  color: white;
  background-color: #9498a2;
  padding: 1rem 1.5rem;
}
.tabular-info .tabular-info-table td:empty::after, .tabular-info .tabular-info-table pre:empty::after {
  font-family: "LiveDashboardFont", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  content: "---";
  opacity: 0.5;
  font-size: 1rem;
}
.tabular-info .tabular-info-table td:first-child {
  font-weight: 700;
}
.tabular-info .tabular-info-table td:last-child {
  width: 70%;
}

/* LiveView specific classes for your customizations */
.phx-click-loading {
  opacity: 0.5;
  transition: opacity 1s ease-out;
}

.phx-disconnected {
  cursor: wait;
}

.phx-disconnected * {
  pointer-events: none;
}